import React, { useEffect, useState } from "react";
import { Alert } from "reactstrap";

import { IJob } from "@core/api-types/entities/job";
import { Loading } from "@ui/components/util/Loading";
import { Indexing } from "../../src/components/Indexing";
import {
  IIndexingDocument,
  IInputTaxonomy,
  IPredictions,
  ITaxonomies,
} from "../../src/components/Indexing/types";
import { buildDocumentAndPredictions } from "../../src/components/Workbench/innodom";

type Props = Readonly<{
  job: IJob;
  token: string;
}>;

export const IndexingReview = ({ job, token }: Props) => {
  const [document, setDocument] = useState<IIndexingDocument | null>(null);
  const [error, setError] = useState<Error | null>(null);
  const [predictions, setPredictions] = useState<IPredictions | null>(null);
  const [taxonomies, setTaxonomies] = useState<ITaxonomies | null>(null);
  const [xmlDocument, setXmlDocument] = useState<Document | null>(null);
  useEffect(() => {
    if (!job || !token || document !== null || predictions !== null || xmlDocument !== null) {
      return;
    }
    const contentRow = job.contents.find(c => c.role === "indexing-reviewed-xml");
    if (!contentRow) {
      setError(new Error("Could not find indexing-reviewed-xml."));
      return;
    }
    fetch(contentRow.uri, {
      headers: {
        Authorization: `Basic ${token}`,
      },
      method: "GET",
    })
      .then(c => c.text())
      .then(c => {
        const imageUris = job.contents
          .filter(cr => cr.role === "zoning-pagehr-png")
          .map(cr => cr.uri);
        const userKey = atob(token).split(":")[0];
        const result = buildDocumentAndPredictions(job, userKey, c, imageUris);
        setDocument(result.document);
        setError(result.error);
        setPredictions(result.predictions);
        setXmlDocument(result.xmlDocument);
      })
      .catch(err => {
        setError(err);
      });
  });
  /*
    if (document === null) {
      const { api_keys } = opts.state.user;
      const imageUris = opts.state.job.contents
        .filter(c => c.role === "zoning-pagehr-png")
        .map(c => `${c.uri}?token=${api_keys.live}`);
      setState({
        ...state,
        ...buildDocumentAndPredictions(
          opts.state.job,
          api_keys.live!,
          opts.state.contents,
          imageUris,
        ),
      });
    }
    if (taxonomies === null) {
      const taxonomyContents = opts.state.job.contents
        .filter(c => c.role === "indexing-taxonomy-json")
        .map(c => c.id);
      Promise.all(taxonomyContents.map(id => opts.actions.fetchContent(id)))
        .then((fetchedTaxonomies: Array<IInputTaxonomy>) => {
          const toLoad: ITaxonomies = {};
          for (const tax of fetchedTaxonomies) {
            toLoad[tax.id] = {
              id: tax.id,
              name: tax.name,
              open: true,
            };
          }
          setTaxonomies(toLoad);
        })
        .catch(err => {
          setState({ ...DEFAULT_STATE, error: err });
        });
    }
    */
  useEffect(() => {
    if (!job || !token || taxonomies !== null) {
      return;
    }
    Promise.all(
      job.contents
        .filter(c => c.role === "indexing-taxonomy-json")
        .map(
          async contentRow =>
            new Promise<IInputTaxonomy>((resolve, reject) => {
              fetch(contentRow.uri, {
                headers: {
                  Authorization: `Basic ${token}`,
                },
                method: "GET",
              })
                .then(r => r.json())
                .then(resolve)
                .catch(err => {
                  reject(err);
                });
            }),
        ),
    )
      .then((rawTaxonomies: Array<IInputTaxonomy>) => {
        const toLoad: ITaxonomies = {};
        for (const tax of rawTaxonomies) {
          toLoad[tax.id] = {
            id: tax.id,
            name: tax.name,
            open: true,
          };
        }
        setTaxonomies(toLoad);
      })
      .catch(err => {
        setError(err);
      });
  });

  if (error !== null) {
    console.error(error);
    return <Alert color="danger">{error.message}</Alert>;
  }
  if (document === null || predictions === null || taxonomies === null || xmlDocument === null) {
    return <Loading text="Loading..." />;
  }
  return (
    <Indexing
      document={document}
      jobId={job.id}
      onSave={async () => {
        return;
      }}
      predictions={predictions}
      review
      saving={false}
      taxonomies={taxonomies}
    />
  );
};
IndexingReview.displayName = "MappingReview";
